.Videos {
    margin-bottom: 120px;

    .v-block__title {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 56px;

        button {
            font-size: 16px;
            font-style: normal;
            color: white;
            border: none;
            cursor: pointer;
            font-weight: 700;
            line-height: 120%;
            letter-spacing: 0.08px;
            padding: 18px 24px;
            border-radius: 8px;
            background: var(--light-secondary-color-50, #F36326);

            &:active {
                opacity: 0.7;
            }
        }
    }

    .right-v__block {
        .iframe-block {
            display: flex;
            align-items: center;
            gap: 16px;

            .iframe__title {
                h4 {
                    max-width: 296px;
                    font-size: 24px;
                    font-style: normal;
                    font-weight: 700;
                    line-height: 120%;
                    letter-spacing: 0.12px;
                    margin-bottom: 24px;
                }

                span {
                    font-size: 12px;
                    font-style: normal;
                    font-weight: 700;
                    line-height: 110%;
                    opacity: 0.5;
                    letter-spacing: 0.096px;
                }
            }
        }
    }

    .video__block {
        display: flex;
        // align-items: center;
        justify-content: space-between;
    }

    .left-v__block div iframe {
        margin-bottom: 24px;
    }

    .left-v__block div {
        h4 {
            max-width: 656px;
            font-size: 24px;
            font-style: normal;
            font-weight: 700;
            line-height: 120%;
            letter-spacing: 0.12px;
            margin-bottom: 24px;
        }

        span {
            color: var(--light-text-color-body-2, #9A9AB0);
            font-size: 12px;
            font-style: normal;
            font-weight: 400;
            line-height: 110%;
            letter-spacing: 0.096px;
        }
    }

}