.PaginationCard {
    width: 100%;
    overflow-x: auto;
    padding-bottom: 20px;
}

.PaginationCard__block {
    display: flex;
    flex-direction: column;
    // align-items: center;
    padding: 20px;
}

.card__title {
    display: flex;
    align-items: center;
    justify-content: space-between;
}


.card {
    display: flex;
    flex-wrap: nowrap;
    overflow-x: auto;
    padding-bottom: 10px;
}

.card__item {
    flex: 0 0 auto;
    width: 300px;
    margin-right: 10px;
    background-color: #f0f0f0;
    padding: 20px;
}

.card__item img {
    width: 100%;
    margin-bottom: 10px;
}

.card__item p {
    font-size: 12px;
    font-style: normal;
    font-weight: 700;
    line-height: 110%;
    letter-spacing: 0.096px;
    margin-top: 24px;
}

.pagination {
    display: flex;
    justify-content: center;
    margin-top: 20px;
}

.pagination__button {
    border: none;
    background-color: transparent;
    padding: 5px 10px;
    margin: 0 5px;
    cursor: pointer;
}

.pagination__button.active {
    transition: 0.5s all;
    background-color: #555;
    color: white;
    border-radius: 3px;
    font-weight: bold;
}

.PaginationCard__block::-webkit-scrollbar {
    height: 8px;
}

.PaginationCard__block::-webkit-scrollbar-track {
    background: #f0f0f0;
}

.PaginationCard__block::-webkit-scrollbar-thumb {
    background-color: #888;
    border-radius: 4px;
}

.PaginationCard__block::-webkit-scrollbar-thumb:hover {
    background-color: #555;
}