.container {
    max-width: 100%;
}


.Hero__block {
    max-width: 1232px;
    display: flex;
    justify-content: space-between;
    padding: 80px 0 50px 0;
    margin: 0 auto;
}

.hero__left span {
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 120%;
    letter-spacing: 0.08px;
    gap: 20px;
    margin-bottom: 29px;
}

.hero__left h1 {
    max-width: 608px;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    font-size: 60px;
    font-style: normal;
    font-weight: 700;
    line-height: 120%;
    letter-spacing: 0.186px;
    padding-bottom: 80px;
}

.Hero-card {

    backdrop-filter: blur(16px);

}

.Hero-card ul {
    display: flex;
    gap: 24px;
}

.Hero-card ul li {

    border-left: 4px solid #109BE9;
}

.Hero-card ul li p {
    max-width: 264px;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 120%;
    /* 19.2px */
    letter-spacing: 0.08px;
    margin-top: 16px;
}

.hero__right h3 {
    font-size: 31px;
    font-style: normal;
    font-weight: 700;
    line-height: 120%;
    /* 37.2px */
    letter-spacing: 0.155px;
    padding-bottom: 23px;
}

.Hero-card2 ul {
    max-width: 504px;
}

.Hero-card2 ul li p {
    font-size: 12px;
    font-style: normal;
    font-weight: 700;
    line-height: 110%;
    /* 13.2px */
    letter-spacing: 0.096px;
    padding-bottom: 8px;
}

.Hero-card2 ul li h3 {
    max-width: 288px;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 120%;
    /* 19.2px */
    letter-spacing: 0.08px;
}
.Hero-card2 ul li  {
   display: flex;
   align-items: center;
   gap: 24px;
   margin-bottom: 16px;
}

.Hero-card li.selected {
    background-color: #eaeaea;
    opacity: 0.8;
  }