* {
    text-decoration: none;
    list-style: none;
}

.Header {
    top: 0;
    position: sticky;
    z-index: 1;
    background-color: #909090;
    border-bottom: 1px solid #FFF;
}

.header__block {
    max-width: 1232px;
    margin: 0 auto;
}

.header__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 26px 0;
}

.Rout__link {
    display: flex;
    align-items: center;
    gap: 40px;
    font-size: 13px;
    font-style: normal;
    font-weight: 500;
    line-height: 120%;
    letter-spacing: 0.08px;

}

a {
    color: #fff;
}

.top__list {
    display: flex;
    align-items: center;
    gap: 40px;
}

.top__list li {
    color: white;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 120%;
    /* 19.2px */
    letter-spacing: 0.08px;
}

.header__bottom {
    padding: 10px 0;
}

.h-bottom__left {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.b-left__list {
    display: flex;
    align-items: center;
    gap: 48px;
}

.b-left__list li {
    color: #fff;
    cursor: pointer;
    font-size: 13px;
    font-style: normal;
    font-weight: 400;
    line-height: 120%;
    letter-spacing: 0.08px;
}

.Search__panel {
    display: flex;
    align-items: center;
    gap: 32px;
}

.Search__panel button {
    display: inline-flex;
    border: none;
    padding: 14px 24px;
    color: white;
    border-radius: 8px;
    cursor: pointer;
    background: var(--light-secondary-color-50, #F36326);
}
.Search__panel button:active{
    opacity: 0.8;
}