.SignUp {
    padding: 104px;
    background: var(--light-primary-color-50, #109BE9);
    margin-bottom: 120px;

    .sign__input form {
        display: flex;
        align-items: center;
        gap: 8px;

        input {
            width: 100%;
            padding: 18px 0 18px 20px;
            border-radius: 8px;
            border: none;
        }

        button {
            color: white;
            border-radius: 8px;
            background: var(--light-secondary-color-50, #F36326);
            padding: 18px 24px;
            font-size: 14px;
            font-style: normal;
            cursor: pointer;
            border: none;
            font-weight: 600;
            line-height: 120%;
            /* 19.2px */
            letter-spacing: 0.08px;

            &:active{
                opacity: 0.7;
            }
        }
    }
}

.sign__text {
    display: flex;
    align-items: end;
    justify-content: space-between;
    margin-bottom: 64px;

    h2 {
        color: white;
        font-size: 62px;
        font-style: normal;
        font-weight: 700;
        line-height: 120%;
        letter-spacing: 0.186px;
    }

    p {
        color: white;
        max-width: 400px;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 125%;
        letter-spacing: 0.08px;
    }
}