.Footer {
    max-width: 1232px;
    margin: 0 auto;
    box-sizing: border-box;

    .Footer__top {
        display: flex;
        align-items: start;
        justify-content: space-between;
        margin-bottom: 23px;

        div img {
            cursor: pointer;
        }

        div p {
            max-width: 249px;
            font-size: 16px;
            font-style: normal;
            font-weight: 400;
            line-height: 125%;
            /* 20px */
            letter-spacing: 0.08px;
            padding-top: 30px;
        }

        ul li:nth-child(1) {
            font-size: 16px;
            font-style: normal;
            font-weight: 700;
            line-height: 120%;
            /* 19.2px */
            letter-spacing: 0.08px;
        }

        ul li {
            padding-bottom: 24px;
            cursor: pointer;
            font-size: 16px;
            font-style: normal;
            font-weight: 400;
            line-height: 120%;
            letter-spacing: 0.08px;

            &:hover {
                font-size: 18px;
                transition: 0.5s all;
                color: #109BE9;
            }
        }
    }

    .Footer__bottom {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 42px;

        h6 {
            color: var(--text-body-text, #666);
            font-size: 16px;
            font-style: normal;
            font-weight: 500;
            line-height: 120%;
        }
        select{
            border: none;
        }
    }
}